import React, { useState } from 'react';
import '../../assets/main.css';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowRight, faArrowLeft } from '@fortawesome/free-solid-svg-icons';
const Slider = () => {
  const [image, setimage] = useState(1);
  const adding = () => {
    if (image !== 5) {
      setimage(image + 1);
    } else {
      setimage(1);
    }
  };
  const removing = () => {
    if (image !== 1) {
      setimage(image - 1);
    } else {
      setimage(5);
    }
  };
  return (
    <div className='flex h-screen bg-blue-400'>
      <div className='mx-auto flex w-2/3'>
        <FontAwesomeIcon
          icon={faArrowLeft}
          className='mr-2 text-5xl my-auto text-white hover:text-blue-500'
          onClick={removing}
        />
        <div className='my-auto rounded-circle'>
          <img
            src={require(`./image${image}.jpg`)}
            alt=''
            className='rounded-lg'
          />
        </div>
        <FontAwesomeIcon
          icon={faArrowRight}
          className='ml-2 text-5xl my-auto text-white hover:text-blue-500'
          onClick={adding}
        />
      </div>
    </div>
  );
};

export default Slider;
