import React, { useState, useEffect } from 'react';
import '../../assets/main.css';
import styled from 'styled-components';
const Counter = () => {
  const [counter, setcounter] = useState(0);
  const [coloring, setcoloring] = useState('black');
  useEffect(() => {
    if (counter < 0) {
      setcoloring('red');
    } else if (counter > 0) {
      setcoloring('green');
    } else {
      setcoloring('black');
    }
  });
  const adding = () => {
    setcounter(counter + 1);
  };
  const removing = () => {
    setcounter(counter - 1);
  };
  // useEffect();
  const CounterForCount = styled.div`
    color: ${coloring};
    text-align: center;
    font-size: 5rem;
  `;

  return (
    <div className='flex bg-purple-400 h-screen'>
      <div className='bg-blue-500 w-2/3 py-16 m-auto'>
        <h1 className='text-5xl uppercase text-center text-white font-serif'>
          Counter
        </h1>
        <CounterForCount>{counter}</CounterForCount>

        <div className='flex justify-around '>
          <button
            onClick={removing}
            className='bg-red-200 w-32 rounded hover:text-white hover:bg-pink-400 hover:rounded-full font-serif'
          >
            Lover Count
          </button>
          <button
            onClick={adding}
            className='bg-red-200 w-32 rounded hover:text-white hover:bg-pink-400 hover:rounded-full font-serif'
          >
            Add Count
          </button>
        </div>
      </div>
    </div>
  );
};

export default Counter;
